import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { ProductsServices } from '../../../../services/products/products.services';
import { productStoreColumn } from './product-store.column';
@Component({
  selector: 'product-store',
  templateUrl: './product-store.component.html',
  styleUrls: ['./product-store.component.scss']
})
export class ProductStoreComponent implements OnInit {
  @ViewChild('dialog') dialog: TemplateRef<any>;
  column: any[] = productStoreColumn;
  productId: any;
  storsData: GridDataResult = {
    data: [],
    total: 0
  }
  typeTitle: string;
  storeId: any;
  editStoreFormGroup: FormGroup;
  loading: boolean = false;
  constructor(
    private _porductService: ProductsServices,
    private activeRouter: ActivatedRoute,
    private _NbDialogService: NbDialogService,
    private toaster: NbToastrService,
    private cdr: ChangeDetectorRef,
    private _DatePipe: DatePipe,
  ) {
    this.editStoreFormGroup = new FormGroup({
      "id": new FormControl(),
      "publish_app_at": new FormControl(null, [Validators.required]),
      "price": new FormControl(null, [Validators.required]),
      "net_price": new FormControl(null, [Validators.required]),
      "discount": new FormControl(null, [Validators.required]),
      "discount_type": new FormControl(null, [Validators.required]),
      "free_shipping": new FormControl(null, [Validators.required]),
    })
  }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getProductStors(this.productId);
    });

    this.editStoreFormGroup.get('discount_type').valueChanges.subscribe(res => {
      if (res) {
        this.editStoreFormGroup.get('discount').enable();
      } else {
        this.editStoreFormGroup.get('discount').reset();
        this.editStoreFormGroup.get('discount').disable();
      }
    })
  }

  getProductStors(id: any) {
    this._porductService.getProductStores(id).subscribe(
      res => {
        console.log(res['data']);
        this.storsData.data = res['data'];
        this.storsData.total = res['data'].length;
      },
      error => { },
      () => { },
    )
  }

  saveStore(dialougRef: any) {
    let paramsData: any = this.editStoreFormGroup.value;

    paramsData['discount_type'] = paramsData['discount_type'] == 'AMOUNT' ? 1 : 2;
    paramsData['publish_app_at'] = this._DatePipe.transform(paramsData['publish_app_at'], 'yyyy-MM-dd');



    this.loading = true;
    this._porductService.saveStoreInfo(paramsData).subscribe(
      res => {
        this.toaster.show(res['message'], 'Success', { status: 'success' });
      },
      error => {
        this.toaster.show(error.error.message, 'Error', { status: 'danger' });
        this.loading = false;
        dialougRef.close();
      },
      () => {
        this.loading = false;
        dialougRef.close();
        this.getProductStors(this.productId);
      },
    );
  }


  commandAction(event: any) {
    if (event.action.action === "edit") {
      this._NbDialogService.open(this.dialog, {
        context: 'Stores',
        closeOnBackdropClick: false,
      });

      setTimeout(() => {
        let dataInDialog: any = {
          "id": event.data.id,
          "publish_app_at": new Date(event.data.publish_app_at),
          "price": event.data.price,
          "net_price": event.data.net_price,
          "discount": event.data.discount,
          "discount_type": event.data.discount_type,
          "free_shipping": event.data.free_shipping,
        };
        this.editStoreFormGroup.setValue(dataInDialog);
      }, 100);

    } else {
      this.storeId = event.data.store_id;
      this.typeTitle = event.action.toolTip;
    }
  }

}
