import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { StockService } from '../../../../services/stock/stock.service';

@Component({
  selector: 'product-stock',
  templateUrl: './product-stock.component.html',
  styleUrls: ['./product-stock.component.scss']
})
export class ProductStockComponent implements OnInit {
  @Input('storeId') storeId: any;
  public productId: any;

  public stockList: any[];
  public loading: boolean = false;
  constructor(
    private _stockService: StockService,
    private activeRouter: ActivatedRoute,
    private toaster: NbToastrService,
  ) { }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getStocks(this.productId, this.storeId);
    });
  }

  getStocks(productId: any, stockId: any) {
    this.loading = true;
    this._stockService.getStocks(productId, stockId).subscribe(
      res => {
        this.stockList = res['data'];
      },
      error => {
        this.loading = false;
        this.toaster.show(error.error.message, 'Error', { status: 'danger' });
      },
      () => {
        this.loading = false;
      },
    )
  }

}
