import { Component, EventEmitter, OnInit, Output, ViewChild } from '@angular/core';

import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { isObject } from 'rxjs/internal/util/isObject';
import { BrandsServices } from '../../../../services/brands/brands.services';
import { CategoriseServices } from '../../../../services/categories/categories.services';
import { ProductsServices } from '../../../../services/products/products.services';
import { SellersServices } from '../../../../services/sellers/sellers.services';
import { MaterialsServices } from '../../../../services/materials/materials.services';
import { PoliciesServices } from '../../../../services/policies/policies.services';
import { ShippingMethodsServices } from '../../../../services/shipping-methods/shipping-methods.services';
import { AttributesListEditComponent } from '../attributes-list-edit/attributes-list-edit.component';

@Component({
  selector: 'product-info',
  templateUrl: './product-info.component.html',
  styleUrls: ['./product-info.component.scss']
})
export class ProductInfoComponent implements OnInit {
  @ViewChild('attributesListEdit') attributesListEdit: AttributesListEditComponent;
  @Output() saveAction: EventEmitter<any> = new EventEmitter();
  productId: any;
  autoCompeletFields: any[] = [
    {
      feildId: 'brand',
      controlName: 'brand',
      placeholder: 'Brand',
      filteredOptions: [],
    },
    {
      feildId: 'category',
      controlName: 'category',
      placeholder: 'Category',
      filteredOptions: [],
    },
    {
      feildId: 'material',
      controlName: 'material',
      placeholder: 'Material',
      filteredOptions: [],
    },
    {
      feildId: 'policy',
      controlName: 'policy',
      placeholder: 'Policy',
      filteredOptions: [],
    },
    {
      feildId: 'shipping_method',
      controlName: 'shipping_method',
      placeholder: 'Shipping Method',
      filteredOptions: [],
    },
    // {
    //   feildId: 'owner',
    //   controlName: 'owner',
    //   placeholder: 'Owner',
    //   filteredOptions: [],
    // },
  ];

  InfoFormGroup: FormGroup;
  loading: boolean = false;
  formType='';


  constructor(
    private route: Router,
    private activeRouter: ActivatedRoute,
    private productService: ProductsServices,
    private brandService: BrandsServices,
    private materialsService: MaterialsServices,
    private policiesServices: PoliciesServices,
    private shippingMethodsService: ShippingMethodsServices,
    private categoryService: CategoriseServices,
    // private sellerService: SellersServices,
  ) {
    this.InfoFormGroup = new FormGroup({
      id: new FormControl(),
      name: new FormControl(),
      description: new FormControl(),
      brand_id: new FormControl(),
      category_id: new FormControl(),
      material_id: new FormControl(),
      material: new FormControl(),
      shipping_method_id: new FormControl(),
      shipping_method: new FormControl(),
      policy_id: new FormControl(),
      policy: new FormControl(),
      owner_id: new FormControl(),
      consumer_price: new FormControl(),
      activation: new FormControl(),
      rate: new FormControl(),
      brand: new FormControl(),
      category: new FormControl(),
      owner: new FormControl(),
      product_attributes: new FormControl(),
      free_shipping: new FormControl(),
      price: new FormControl(),
      discount: new FormControl(),
      publish_app_at: new FormControl(),
    })
  }

  ngOnInit(): void {
    if (this.route.url.includes('/products/sellers')) {
      this.formType = 'sellers';
    }
    if (this.route.url.includes('/products/stores')) {
      this.formType = 'stores';
    }
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getProductInfo();
    });

    this.InfoFormGroup.get('id').disable();
    this.InfoFormGroup.get('owner').disable();
    this.InfoFormGroup.get('rate').disable();
  }

  getProductInfo() {
    this.loading = true;
    this.productService.getProductInfo(this.productId).subscribe(
      res => {
        this.InfoFormGroup.patchValue(res['data']);
      },
      error => {
        this.loading = false;
      },
      () => {
        this.loading = false;
      },
    )
  }

  getBrands(name: any) {
    let meta: any = 'name=' + name;
    this.brandService.getBrands(meta).subscribe(
      res => {
        this.autoCompeletFields[0].filteredOptions = res.data;
      },
      error => { },
      () => { },
    )
  }

  getMaterials(name: any) {
    let meta: any = 'name=' + name;
    this.materialsService.getMaterials(meta).subscribe(
      res => {
        this.autoCompeletFields[2].filteredOptions = res['data'];
      },
      error => { },
      () => { },
    )
  }

  getPolicies(name: any) {
    let meta: any = 'name=' + name;
    this.policiesServices.getPolicies(meta).subscribe(
      res => {
        this.autoCompeletFields[3].filteredOptions = res['data'];
      },
      error => { },
      () => { },
    )
  }

  getShippingMethods(name: any) {
    let meta: any = 'name=' + name;
    this.shippingMethodsService.getShippingMethods(meta).subscribe(
      res => {
        this.autoCompeletFields[4].filteredOptions = res['data'];
      },
      error => { },
      () => { },
    )
  }

  



  getCategorise(name: any) {
    let meta: any = 'name_en=' + name;
    this.categoryService.getCategorise(meta).subscribe(
      res => {
        this.autoCompeletFields[1].filteredOptions = res.data;
      },
      error => { },
      () => { },
    )
  }
  // getOwner(name: any) {
  //   let meta: any = 'name=' + name + '&activation=true';
  //   this.sellerService.getSellers(meta).subscribe(
  //     res => {
  //       this.autoCompeletFields[2].filteredOptions = res.data;
  //     },
  //     error => { },
  //     () => { },
  //   )
  // }

  onInputAutoCompleteChange(field: any) {
    console.log(field.feildId);
    if (field.feildId === "brand") {
      this.getBrands(this.InfoFormGroup.get(field.controlName).value);
    }

    if (field.feildId === "category") {
      this.getCategorise(this.InfoFormGroup.get(field.controlName).value);
    }

    if (field.feildId === "material") {
      this.getMaterials(this.InfoFormGroup.get(field.controlName).value);
    }

    if (field.feildId === "policy") {
      this.getPolicies(this.InfoFormGroup.get(field.controlName).value);
    }

    if (field.feildId === "shipping_method") {
      this.getShippingMethods(this.InfoFormGroup.get(field.controlName).value);
    }

    // if (field.feildId === "owner") {
    //   this.getOwner(this.InfoFormGroup.get(field.controlName).value);
    // }
  }

  onSelectionChange(event: any, field: any) {
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.name ? value.name : value.name_en;
    } else {
      return value;
    }
  }

  backToProducts() {
    this.route.navigate(['products/'+this.formType])
  }
}
