import { DatePipe } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { ProductsServices } from '../../../../services/products/products.services';
import { ProductInfoComponent } from '../product-info/product-info.component';


@Component({
  selector: 'app-product-details',
  templateUrl: 'product-details.component.html',
  styleUrls: ['product-details.component.scss'],
})
export class ProductDetailsComponent implements OnInit {
  @ViewChild('productInfo') productInfo: ProductInfoComponent;
  allBrands: any[];
  allCategorys: any[];
  allSellers: any[];
  loading: boolean = false;
  productId: any;

  constructor(
    private _ProductsServices: ProductsServices,
    private _ActivatedRoute: ActivatedRoute,
    private toaster: NbToastrService,
    private _DatePipe: DatePipe,
  ) {
  }

  ngOnInit() {
    this.productId = this._ActivatedRoute.snapshot.params.id;
  }

  saveProductInfo(value: any) {
    // this.productInfo;
    console.log(this.productInfo.attributesListEdit.attrList);
    console.log(value);
    this.loading = true;
    let params: any = {
      "id": this.productId,
      "name": value.name,
      "description": value.description,
      "brand_id": value.brand?.id,
      "category_id": value.category.id,
      "consumer_price": value.consumer_price,
      "material_id": value.material?.id,
      "policy_id": value.policy?.id,
      "shipping_method_id": value.shipping_method?.id,
      "activation": value.activation,
      "free_shipping": value.free_shipping,
      "price": value.price,
      "discount": value.discount,
      "publish_app_at": this._DatePipe.transform(value.publish_app_at, 'yyyy-MM-dd'),
      "product_attributes": this.productInfo.attributesListEdit.attrList.map(ele => { return { size_id: ele.size.id, quantity: ele.quantity } })
    }
    this._ProductsServices.saveProductInfo(params).subscribe(
      res => {
        this.toaster.show(res['message'], 'Success', { status: 'success' });
      },
      error => {
        this.toaster.show(error.error.message, 'Error', { status: 'danger' });
        this.loading = false;
      },
      () => {
        this.loading = false;
        this.productInfo.backToProducts();
      },
    )
  }
}