import { Component, Input, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { BundlesService } from '../../../../services/bundles/bundles.service';

@Component({
  selector: 'product-bundles',
  templateUrl: './product-bundles.component.html',
  styleUrls: ['./product-bundles.component.scss']
})
export class ProductBundlesComponent implements OnInit {

  @Input('storeId') storeId: any;
  public productId: any;
  public loading: boolean = false;
  public bundlesList: any[] = [];

  constructor(
    private _bundleService: BundlesService,
    private activeRouter: ActivatedRoute,
    private toaster: NbToastrService,
  ) {
  }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getBundles(this.productId, this.storeId);
    });
  }

  getBundles(productId: any, storeId: any) {
    this.loading = true;
    this._bundleService.getProductBundles(productId, storeId).subscribe(
      res => {
        this.bundlesList = res['data'];
      },
      error => {
        this.loading = false;
        this.toaster.show(error.error.message, 'Error', { status: 'danger' });
      },
      () => {
        this.loading = false;
      },
    )
  }

}
