import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { isObject } from 'rxjs/internal/util/isObject';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { ProductsServices } from '../../../../services/products/products.services';
import { SizesServices } from '../../../../services/sizes/sizes.services';

@Component({
  selector: 'attributes-list-edit',
  templateUrl: './attributes-list-edit.component.html',
  styleUrls: ['./attributes-list-edit.component.scss']
})
export class AttributesListEditComponent implements OnInit {
  @Output() attrListUpdates = new EventEmitter();

  public sizeList: any[] = [];
  public attrList: any[] = [];
  public sizeFormControl: FormControl = new FormControl('', Validators.required);
  public quantityFormControl: FormControl = new FormControl('', Validators.required);
  public autoLoad: boolean = false;
  productId: any;
  loading: boolean = false;


  constructor(
    private _SizesServices: SizesServices,
    private activeRouter: ActivatedRoute,
    private productService: ProductsServices,

  ) { }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getProductInfo();
    });
    console.log("monem");
    this.getSizes();
    this.sizeFormControl.valueChanges.pipe(distinctUntilChanged(), debounceTime(800)).subscribe(res => {
      if (res && !isObject(res)) {
        this.autoLoad = true;
        const meta = 'size=' + res;
        this.getSizes(meta);
      }
    });
  }

  getProductInfo() {
    this.loading = true;
    this.productService.getProductInfo(this.productId).subscribe(
      res => {
        if (res['data'].product_attributes) {
          res['data'].product_attributes.forEach(element => {
            const newAtrr: any = {
              size: {
                id: element.size.id,
                name: element.size.size,
              },
              quantity: element.quantity,
            }
            this.attrList.push(newAtrr)
          });

        }
        // this.InfoFormGroup.patchValue(res['data']);
      },
      error => {
        this.loading = false;
      },
      () => {
        this.loading = false;
      },
    )
  }

  viewHandle(value: any) {
    if (isObject(value)) {
      return value.size ? value.size : value.name;
    } else {
      return value;
    }
  }

  getSizes(meta?: any) {
    this._SizesServices.getSizes(meta).subscribe(
      (res: any) => {
        this.sizeList = res.data;
      },
      error => {
        this.autoLoad = false;
      },
      () => {
        this.autoLoad = false;
      },
    )
  }

  addNewAttr() {
    if (!this.attrList?.some(ele => this.sizeFormControl.value.id === ele.size.id)) {
      const newAtrr: any = {
        size: {
          id: this.sizeFormControl.value.id,
          name: this.sizeFormControl.value.size,
        },
        quantity: this.quantityFormControl.value,
      }
      this.attrList.push(newAtrr)
      this.attrListUpdates.emit(this.attrList);
      this.sizeFormControl.reset('');
      this.quantityFormControl.reset();
    }
  }

  onTagRemove(index: any) {
    this.attrList.splice(index, 1);
    this.attrListUpdates.emit(this.attrList);
  }
}