import { Component, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { NbToastrService } from '@nebular/theme';
import { ProductsServices } from '../../../../services/products/products.services';

@Component({
  selector: 'product-image',
  templateUrl: './product-image.component.html',
  styleUrls: ['./product-image.component.scss']
})
export class ProductImageComponent implements OnInit {

  products: any[] = [];

  fileSelected: File;
  imageList: any[] = [];
  defaultImgLink: string = "/assets/icons/picture.svg";
  currentImage: any = this.defaultImgLink;
  productId: any;

  bardcodeValue: string;
  colorAttr: any;
  barCodeControl: FormControl = new FormControl();

  constructor(
    private activeRouter: ActivatedRoute,
    private _toasterService: NbToastrService,
    private _productService: ProductsServices,
  ) {

  }

  ngOnInit(): void {
    this.activeRouter.params.subscribe(params => {
      this.productId = params['id'];
      this.getImageBardcode();
    });

    this.barCodeControl.valueChanges.subscribe(
      res => {
        this.bardcodeValue = res;
      }
    )
  }

  getImageBardcode() {
    this._productService.getProductImageBardcode(this.productId).subscribe(
      res => {
        this.products = res['data'];

        this.imageList = res['data'][0]?.images?.map((ele: any) => ele.image);
        if (this.imageList) {
          this.currentImage = this.imageList.length > 0 ? this.imageList[0] : this.defaultImgLink;
          this.bardcodeValue = res['data'][0].barcode;
          this.colorAttr = res['data'][0].color;
        }
      },
      error => { },
      () => { },
    )
  }

  public handleFileInput(event) {
    let files = event.target?.files;
    let reader = new FileReader();
    reader.readAsDataURL(files[0]);
    reader.onload = (_event) => {
      this.addNewImg(reader.result);
    }
  }

  addNewImg(newImage: any) {
    if (this.imageList?.some((img: any) => img === newImage)) {
      this._toasterService.show('Image Already Selected', 'Error', { status: 'danger' });
    } else {
      this.currentImage = newImage;
      this.imageList.push(newImage);
    }
  }

  chnageMainImg(selectedImage: any) {
    this.currentImage = selectedImage;
  }

  removeImage(deleteImage: any) {
    this.imageList = this.imageList.filter((img: any) => img !== deleteImage);
    if (this.currentImage === deleteImage) {
      this.currentImage = this.imageList[0] ? this.imageList[0] : this.defaultImgLink;
    }
  }

  selectProduct(selectedProduct: any) {
    this.imageList = selectedProduct.images?.map((ele: any) => ele.image);
    this.currentImage = this.imageList.length > 0 ? this.imageList[0] : this.defaultImgLink;
    this.bardcodeValue = selectedProduct.barcode;
    this.colorAttr = selectedProduct.color;
  }

}
